* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Blinker, sans-serif;
}

:root {
  --index: calc(1vw + 1vh);
  --text: #e7e7e0;
  --transition: transform .75s cubic-bezier(.075, .5, 0, 1);
}

body {
  background-image: url("layer-base.jpg.a3d0e0e5.jpg");
  font-family: raleway_f, sans-serif;
  line-height: 1.55;
}

.main-header {
  position: relative;
}

.main-header:after {
  content: "";
  z-index: 100;
  width: 100%;
  height: calc(var(--index) * 10);
  bottom: calc(var(--index) * -4.5);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
}

.layers {
  height: 100vh;
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.layer {
  height: 100%;
  width: 100%;
  will-change: transform;
  z-index: 2;
  transition: var(--transition);
  background-position: center;
  background-size: cover;
  position: absolute;
}

.layers__base {
  transform: translate3d(0, calc(var(--scrollTop) / 1.6), 0);
  z-index: 0;
}

.layer__middle {
  transform: translate3d(0, calc(var(--scrollTop) / 2.5), 0);
}

.layer__front {
  transform: translate3d(0, calc(var(--scrollTop) / 5.7), 0);
}

.layers__header {
  z-index: 1;
  transform: translate3d(0, calc(var(--scrollTop) / 2), 0);
  will-change: transform;
  text-transform: uppercase;
  color: var(--text);
  text-shadow: 0 0 15px #9e7d35;
  transition: var(--transition);
  font-weight: 900;
}

.layers__title {
  color: #000;
  letter-spacing: calc(var(--index) / 2.25);
  font-size: 60px;
}

.layers__caption {
  font-size: calc(var(--index) / 1.175);
  letter-spacing: calc(var(--index) / 3.5);
  margin-top: calc(var(--index) * .75);
}

.main-article {
  --main-article-transform: translate3d(0, calc(var(--scrollTop) / -7.5), 0);
  min-height: 100vh;
  color: #000;
  text-align: center;
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.main-article__header {
  text-transform: uppercase;
  font-size: calc(var(--index) * 1.8);
  letter-spacing: calc(var(--index) / 7.5);
  transform: var(--main-article-transform);
  will-change: transform;
  transition: var(--transition);
}

.main-article__paragraph {
  max-width: calc(var(--index) * 30);
  font-size: calc(var(--index) * .9);
  margin-top: calc(var(--index) / 1.25);
  transform: var(--main-article-transform);
  transition: var(--transition);
  will-change: transform;
}

.copy {
  bottom: calc(var(--index) * 2.5);
  opacity: .45;
  font-size: calc(var(--index) * .75);
  letter-spacing: calc(var(--index) / 37.5);
  position: absolute;
}

.content {
  will-change: transform;
}

.breed-select {
  z-index: 200;
  width: 250px;
  position: absolute;
  top: 5%;
  left: 45%;
  transform: translate(-5%, -50%);
  box-shadow: 0 0 15px #9e7d35;
}

.container {
  z-index: 122;
  min-height: 650px;
  opacity: 0;
  pointer-events: none;
  visibility: none;
  background-color: rgba(1, 1, 200, .05);
  border-radius: 10px;
  padding: 20px;
  transition: all .25s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(.6)rotate(180deg);
  box-shadow: 0 0 15px #9e7d35;
}

.is-active {
  visibility: visible;
  pointer-events: all;
  opacity: 1;
  transition: all .25s ease-in-out .25s;
  transform: translate(-50%, -50%)scale(1)rotate(0deg);
}

.intro {
  text-align: left;
}

.cat-info {
  min-width: 700px;
  border-radius: 4px;
  display: block;
}

.img-cat {
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 3%;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  box-shadow: 0 0 8px rgba(241, 245, 162, .47);
}

.close-btn {
  position: absolute;
  top: 18px;
  right: 18px;
}

.cat-info {
  color: #070707;
  margin: 0 0 30px;
  padding: 0;
  font-size: 20px;
}

.close-btn {
  width: 40px;
  cursor: pointer;
  position: absolute;
  top: 25px;
  right: 25px;
}

.cl-btn-6-in {
  width: inherit;
  text-align: center;
}

.cl-btn-6-txt {
  text-transform: uppercase;
  color: #000;
  opacity: 0;
  cursor: pointer;
  font-family: Verdana;
  font-size: 10px;
  font-weight: 700;
  line-height: 40px;
  transition: all .3s ease-in;
}

.cl-btn-6-in:before, .cl-btn-6-in:after {
  content: "";
  height: 4px;
  width: inherit;
  background: #000;
  transition: all .3s ease-in;
  position: absolute;
  left: 0;
}

.cl-btn-6-in:before {
  top: calc(50% - 4px);
  transform: rotate(45deg);
}

.cl-btn-6-in:after {
  bottom: 50%;
  transform: rotate(-45deg);
}

.close-btn:hover .cl-btn-6-txt {
  opacity: 1;
}

.close-btn:hover .cl-btn-6-in:before, .close-btn:hover .cl-btn-6-in:after {
  transform: rotate(0);
}

.close-btn:hover .cl-btn-6-in:before {
  top: 0;
}

.close-btn:hover .cl-btn-6-in:after {
  bottom: 0;
}

.breed-select {
  color: #000;
  z-index: 120;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 5px;
  padding: 10px;
  font-size: 20px;
}

.breed-select option {
  color: #000;
}

.breed-select:hover, .breed-select:active, .breed-select:focus {
  color: #fff;
  background-color: #0979ce;
  border-color: #fff;
}

.loader {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader:before {
  content: "";
  width: 48px;
  height: 48px;
  background: linear-gradient(to right, #410cd3 50%, #fffb00 50%) 100% 0 / 200% no-repeat;
  border-radius: 50%;
  animation: colorBallMoveX 1.5s linear infinite alternate;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-5px, -50%);
}

.loader:after {
  content: "";
  width: 2px;
  height: 100%;
  background: #000;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes colorBallMoveX {
  0% {
    background-position: 0 0;
    transform: translate(-15px, -50%);
  }

  15%, 25% {
    background-position: 0 0;
    transform: translate(0, -50%);
  }

  75%, 85% {
    background-position: 100% 0;
    transform: translate(50px, -50%);
  }

  100% {
    background-position: 100% 0;
    transform: translate(65px, -50%);
  }
}

